import {
	getCompany,
	getJob,
	getArchives,
	getAdvertising,
} from '@/api/job'
import {
	$wuxToast
} from '../../wxcomponents/wux/index'
import citys from '@/utils/data'
import {
	mapGetters
} from 'vuex'
export default {
	computed: {
		...mapGetters([
			'address',
		])
	},
	data() {
		return {
			ready: false,
			notice: null,
			advertising: {
				height: '180px',
				list: null,
				search: {
					type: 101,
					page: 1,
					limit: 5
				}
			},
			job: {
				list: null,
				search: {
					page: 1,
					limit: 5
				}
			},
			company: {
				// 热门企业
				list: null,
				search: {
					page: 1,
					limit: 5
				}
			},
			archives: {
				// 求职攻略
				list: null,
				search: {
					arctype: '1,2,3,4,5',
					page: 1,
					limit: 5
				}
			},
			locations: {
				province: null,
				city: null,
				area: null,
				citys: '全国',
			},
			search: {
				value: null,
			},
			city: {
				show: false,
				options: citys
			}
		}
	},
	onShow() {
		uni.hideKeyboard()
		this.$store.commit('SET_LTABBAR', 'home');
	},
	onReady() {
		this.ready = true
	},
	onLoad() {
		uni.hideTabBar()
		this.getAdvertising();
	},
	methods: {
		getAdvertising() {
			// 头部轮播图
			const t = this.advertising
			getAdvertising(t.search).then(response => {
				let r = response.data
				if (response.code === 20000) {
					t.list = r.list
					this.getCompany();// 热门企业
					this.getJob();// 为你精选好的职位
					this.getArchives();// 求职攻略
				} else {
					$wuxToast().show({
						type: 'forbidden',
						text: '请求异常'
					})
				}
			})
		},
		getCompany() {
			// 热门企业
			const t = this.company
			getCompany(t.search).then(response => {
				let r = response.data
				if (response.code === 20000) {
					t.list = r.list
				} else {
					$wuxToast().show({
						type: 'forbidden',
						text: '请求异常'
					})
				}
			})
		},
		getJob() {
			// 为你精选好的职位
			const s = this.job.search
			const t = this.job
			const a = this.address
			const search = Object.assign({}, {
					page: s.page,
					limit: s.limit,
				},
				a.province ? {
					province: a.province
				} : {},
				a.city ? {
					city: a.city
				} : {},
				a.area ? {
					area: a.area
				} : {},
			);
			getJob(search).then(response => {
				console.log(response);
				let r = response.data
				if (response.code === 20000) {
					t.list = r.list
				} else {
					$wuxToast().show({
						type: 'forbidden',
						text: '请求异常'
					})
				}
			})
		},
		// getArchives : async function(){    
		// 		console.log("哈哈哈哈哈哈");
		//         const t = this.archives
		//         let res = await getDevice(t)     //这样写的好处是不存在异步处理，不会影响数据处理顺序
		//         console.log(res+"：新的方法")
		//     },
		getArchives() {
			// 求职攻略
			const t = this.archives
			getArchives(t.search).then(response => {
				let r = response.data
				if (response.code === 20000) {
					t.list = r.list
				} else {
					$wuxToast().show({
						type: 'forbidden',
						text: '请求异常'
					})
				}
			})
		},
		imageLoad(e) {
			const winWid = uni.getSystemInfoSync().windowWidth
			const ratio = e.detail.width / e.detail.height
			this.advertising.height = winWid / ratio + 'px'
		},
		editCity(e) {
			// 选择城市
			let s = this.locations
			let address = e.detail.options.map((n) => n.label)
			let arr = e.detail.options.map((n) => n.label)
			if (arr.length === 1) {
				s.citys = address[0]
			} else if (arr.length === 2) {
				s.citys = address[1]
			} else if (arr.length === 3) {
				s.citys = address[2]
			}
			s.province = arr[0]
			s.city = arr[1]
			s.area = arr[2]
			this.$store.commit('SET_ADDRESS', s)
		},
		detailTo(i, n) {
			if (n === 1) {
				uni.navigateTo({
					url: '/pages/company/detail?id=' + i
				});
			} else if (n === 2) {
				uni.navigateTo({
					url: '/pages/job/detail?id=' + i
				});
			} else if (n === 3) {
				uni.navigateTo({
					url: '/pages/article/detail?id=' + i
				});
			}
		},
		companyTo() {
			uni.switchTab({
				url: '/pages/company/index'
			});
		},
		jobTo() {
			uni.switchTab({
				url: '/pages/job/index'
			});
		},
		strategyTo() {
			uni.navigateTo({
				url: '/pages/article/strategy'
			});
		},
		getSearch(e) {
			setTimeout(() => {
				if (e.detail) {
					uni.reLaunch({
						url: '/pages/job/index?search=' + e.detail
					});
				} else {
					$wuxToast().show({
						type: 'forbidden',
						text: '未填写职位'
					})
				}
			}, 500)
		},
	}
}
